<template>
  <b-container class="dashboard">
    <RepoNav v-if="$route.name != 'Topic'"/> 

    <b-card style="width: 60%">
      <b-form>
        <b-form-input
          v-model="title"
          placeholder="Title"
          required
          style="margin-bottom: 10px"
        ></b-form-input>
        <b-form-textarea
          v-model="commet"
          placeholder="Leave a commet"
          size="md"
          rows="5"
          max-rows="10"
          style="margin-bottom: 10px"
        ></b-form-textarea>
        <b-button variant="success" @click="onSubmit">Submit new issue</b-button>
      </b-form>
    </b-card>

  </b-container>
</template>

<script>
import RepoNav from "@/components/RepoNav";
import axios from 'axios';
export default {
  name: "Issues",
  data() {
    return {
      title: '',
      commet: ''
    };
  },
  methods: {
    fullRepoName() {
      return this.$route.params.owner + "/" + this.$route.params.repo;
    },
    onSubmit() {
      axios({
        method: 'GET',
        url: '/'+this.fullRepoName()+'/issues/new',
        params: {
          ajax: 1,
          owner: this.$route.params.owner,
          repo: this.$route.params.repo,
          title: this.title,
          commet: this.commet
        }
      }).then(response => {
        if(response.data.state == 'ok') {
          this.$router.replace('/index');
        }else {
          console.log(response.data.error);
        }
      });
    }
  },
  components: { RepoNav }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.dashboard > :not(first-child) {
  margin-top: 1%;
}

</style>
